body {
	margin: 0;
	padding: 0;
	background: #FFF;
	font-family: 'Quicksand', sans-serif !important;
	font-weight: 100;
	color: #4D606E;
}

.plus-icon {
	fill: white;
}

header {
	text-align: center;
	margin: 0px 0 0 0;
}

.logo {
	width: 270PX;
	height: 200PX;
}

.menu-buttons {
	position: fixed;
	z-index: 99;
	right: 10px;
	top: 20px;
	width: 80px;
	text-align: center;

	button, a {
		display: block;
		width: 48px;
		height: 48px;
		border-radius: 100%;
		border: none;
		background: #FFF;
		cursor: pointer;
		text-decoration: none;

		&:first-of-type {
			margin-bottom: 12px;
		}
	}
}

.profile-button {
	background-image: url(../img/profielfoto.png) !important;
	background-position: center !important;
	background-size: cover !important;
}

.map-button {
	background-image: url(../svg/map-icon.svg) !important;
	background-position: center;
	background-size: cover;
}

.main-content {
	width: 962px;
	margin: auto;
}

.add-button {
	font-family: 'Quicksand', sans-serif;
	font-size: 15px;
	background-color: #EA5455;
	color: white;
	margin: 0 auto;
	display: block;
	padding: 12px;
	cursor: pointer;
	border: none;
	border-radius: 5px;
	width: 50%;

	&:before {
		background: url("../svg/plus-icon.svg") no-repeat scroll center center/100% auto transparent;
		content: "";
		display: inline-block;
		color: #fff;
		height: 15px;
		position: relative;
		top: -2px;
		vertical-align: middle;
		width: 15px;
		margin: 0 10px 0 0;
	}
}

.fixed-position {
	position: fixed;
	z-index: 99;
	top: 50px;
	left: 25%;
}

.info-balk {
	margin: 15px 0 5px 0;
	font-weight: 500;

	p {
		display: inline-block;
		padding-left: 20px;
	}
}

select {
	border: none;
	outline: none;
	background: #FFF;
	font-size: 1em;
	font-family: 'Quicksand', sans-serif;
	font-weight: 100;
	color: #EA5455;
}

.post-counter {
	float: right;
}

.message-wall {
	border: 1px solid #EAEAEA;
}

.wall-post {
	border: 1px solid #3D84A8;
	display: inline-block;
	margin-bottom: 6px;
}

.grid-item {
	width: 318px;
	height: 265px;
	float: left;
}

h1 {
	font-size: 15pt !important;
	padding: 8px !important;
	background: #3D84A8;
	color: #FFF;
}

.grid-item--width2 {
	width: 300px;
}

.grid-item--height2 {
	height: 290px;
}

.post-message {
	background-color: #3D84A8;
	margin: 0;
	font-weight: 500;
	font-size: 1.4em;
	line-height: 1em;
	height: 150px;
	padding-top: 16px;
	padding-left: 16px;
	padding-right: 16px;
	color: #FFF;
}

footer {
	padding-left: 5px;
}

.post-img {
	width: 100%;
	height: auto;
}

.like-counter,
.dislike-counter,
.fav-counter,
.dislike-icon,
.fav-icon,
.like-icon {
	display: inline-block;
	float: left;
	font-weight: 700;
}

.like-counter,
.dislike-counter,
.fav-counter {
	margin: 20px 0 0 0;
	padding: 0;
}

.like-counter {
	color: #EA5455;
}

.dislike-counter {
	color: #4D606E;
}

.fav-counter {
	color: #FAB57A;
}

.location {
	display: inline-block;
	float: left;
	margin-top: 15px;
	margin-left: 10px;
	margin-right: 20px;
}

.icon {
	width: 18px;
	height: 18px;
	margin: 17px 5px 8px 5px;
}

.post-profile-picture {
	display: inline-block;
	width: 32px;
	height: 32px;
	margin-top: 10px;
	margin-right: 8px;
	float: right;
}

.map {
	width: 100%;
	height: 80%;
}

#modal {
	display: none;
	position: fixed;
	left: 0;
	top: 0;
	z-index: 100;
	width: 100vw;
	height: 100vh;
	background-color: rgba(255, 255, 255, 0.95);
}